.menu {
	list-style: none;
	margin: 0;
	padding-left: 0;
}

.main-navigation {
	clear: both;
	display: block;
	float: left;
	width: 100%;

	ul {
		display: none;

		a {
			padding:0 1rem 0 0;
		}

		ul {
			box-shadow: 0 3px 3px rgba(0, 0, 0, 0.2);
			background:white;
			float: left;
			position: absolute;
			top: 1.5rem;
			left: -999rem;
			z-index: 99999;

			ul {
				left: -999rem;
				top: 0;
			}

			li {
				&:hover > ul,
				&.focus > ul {
					left: 100%;
				}
			}

			a {
				width: 200px;
			}

			:hover > a,
			.focus > a {
			}

			a:hover,
			a.focus {
			}
		} // ul ul

		li:hover > ul,
		li.focus > ul {
			left: auto;
		}
	} // ul

	li {
		float: left;
		position: relative;

		&:hover > a,
		&.focus > a {
		}
	} // li

	a {
		display: block;
		text-decoration: none;
	}

	.current_page_item > a,
	.current-menu-item > a,
	.current_page_ancestor > a,
	.current-menu-ancestor > a {
	}
}

/* Small menu. */
.menu-toggle,
.main-navigation.toggled ul {
	display: block;
}

@media screen and (min-width: 37.5rem) {
	.menu-toggle {
		display: none;
	}
	.main-navigation ul {
		display: block;
	}
}

.comment-navigation,
.posts-navigation,
.post-navigation {

	.site-main & {
		margin: 0 0 1.5rem;
		overflow: hidden;
	}

	.nav-previous {
		float: left;
		width: 50%;
	}

	.nav-next {
		float: right;
		text-align: right;
		width: 50%;
	}
}
